.maplat .swiper-slide img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  z-index: 0;
}
.maplat .swiper-slide div {
  font-size: 1em;
  position: absolute;
  right: 0;
  bottom: 0.1em;
  left: 0;
  margin: auto;
  z-index: 1;
  color: white;
  background-color: rgba(32, 32, 32, 0.5);
}
.maplat .swiper-slide div.minimize {
  font-size: 0.8em;
}
.maplat .single-map .swiper-left-icon,
.maplat .single-map .swiper-right-icon {
  display: none;
}
.maplat .swiper-left-icon {
  position: absolute;
  left: 0px;
  top: calc(50% - 5px);
  width: 10px;
  height: 10px;
  color: #ffffff;
  z-index: 10;
}
.maplat .swiper-right-icon {
  position: absolute;
  left: calc(100% - 10px);
  top: calc(50% - 5px);
  width: 10px;
  height: 10px;
  color: #ffffff;
  z-index: 10;
}
@media screen and (max-width: 480px) {
